/* eslint-disable no-param-reassign */
import getData from './dataGetter';
import MoviesDB from '../data/MoviesDB';
// import urlParse from '../routes/urlParse';

const pagination = (containerPagination, idGenre) => {
  let page = 1;
  const containerClass = containerPagination.getAttribute('class');
  containerPagination.numberPage = page;

  const next = async () => {
    page++;
    containerPagination.numberPage = page;

    const setPosision = containerPagination.getBoundingClientRect().top + window.pageYOffset - 85;
    window.scrollTo({
      top: setPosision,
      behavior: 'smooth',
    });

    if (containerClass === 'now-playing') {
      getData(containerPagination, MoviesDB.MoviesNowPlaying(page));
      return;
    }
    if (containerClass === 'all-upcomming') {
      getData(containerPagination, MoviesDB.Upcomming(page));
      return;
    }
    if (containerClass === 'trending') {
      getData(containerPagination, MoviesDB.Trending(page));
      return;
    }
    if (containerClass === 'on-air') {
      getData(containerPagination, MoviesDB.TvOnAir(page));
      return;
    }
    if (containerClass === 'top-rated') {
      getData(containerPagination, MoviesDB.TopRated(page, 'tv'));
      return;
    }
    if (containerClass === 'popular-tv') {
      getData(containerPagination, MoviesDB.PopularMovieOrTv(page, 'tv'));
      return;
    }
    if (containerClass === 'genre') {
      getData(containerPagination, MoviesDB.MovieOrTvByGenre(idGenre, page));
    }
  };

  const previous = async () => {
    if (page < 1) {
      return;
    }
    page--;
    containerPagination.numberPage = page;

    const setPosision = containerPagination.getBoundingClientRect().top + window.pageYOffset - 85;
    window.scrollTo({
      top: setPosision,
      behavior: 'smooth',
    });

    if (containerClass === 'now-playing') {
      getData(containerPagination, MoviesDB.MoviesNowPlaying(page));
      return;
    }
    if (containerClass === 'all-upcomming') {
      getData(containerPagination, MoviesDB.Upcomming(page));
      return;
    }
    if (containerClass === 'trending') {
      getData(containerPagination, MoviesDB.Trending(page));
      return;
    }
    if (containerClass === 'on-air') {
      getData(containerPagination, MoviesDB.TvOnAir(page));
      return;
    }
    if (containerClass === 'top-rated') {
      getData(containerPagination, MoviesDB.TopRated(page, 'tv'));
      return;
    }
    if (containerClass === 'popular-tv') {
      getData(containerPagination, MoviesDB.PopularMovieOrTv(page, 'tv'));
      return;
    }
    if (containerClass === 'genre') {
      getData(containerPagination, MoviesDB.MovieOrTvByGenre(idGenre, page));
    }
  };

  containerPagination.next = next;
  containerPagination.previous = previous;
};

export default pagination;
