/* eslint-disable no-param-reassign */
const renderResult = (container, data, title) => {
  if (data < 1) {
    document.querySelector(`#${container.getAttribute('id')}`).remove();
    return;
  }
  container.title = title;
  container.value = data;
};

const getData = async (container, data) => {
  try {
    const result = await data;
    renderResult(container, result, container.getAttribute('id'));
  } catch (error) {
    console.log(error);
  }
};

export default getData;
